import React, { Component } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { withRouter } from "react-router-dom";
import { Tabs } from "antd";
import _ from "lodash";
import {
    setPageButtonData,
    setPageTemplateData,
    setPageActiveKey
} from "Store/AppManagement/action";
import CoverPosotion from "Components/CoverPosition";
import Ajax from "Pub/js/ajax";
import TableResizableColumn from "Components/TableResizableColumn";
import { langCheck } from "Pub/js/utils";
import { getStore } from "Pub/js/storageUtils";
import Notice from "Components/Notice";
const TabPane = Tabs.TabPane;
class PageTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            iserror: false
        };
        this.columnsBtn = [
            {
                title: langCheck(
                    "101818AM-000057",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 序号*/,
                dataIndex: "btnorder",
                width: 50,
                render: text => text + 1
            },
            {
                title: langCheck(
                    "101818AM-000096",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 按钮编码*/,
                dataIndex: "btncode",
                width: 100,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000097",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 按钮名称*/,
                dataIndex: "btnname",
                width: 100,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000098",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 按钮类型*/,
                dataIndex: "btntype",
                width: 100,
                render: (text, record) =>
                    this.renderColumns(text, record, "select", "type")
            },
            {
                title: langCheck(
                    "101818AM-000112",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 按钮颜色*/,
                dataIndex: "btncolor",
                width: 100,
                render: (text, record) =>
                    this.renderColumns(text, record, "select", "color")
            },
            {
                title: langCheck(
                    "101818AM-000099",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 父按钮编码*/,
                dataIndex: "parent_code",
                width: 100,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000100",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 按钮区域*/,
                dataIndex: "btnarea",
                width: 100,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000111",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 快捷键*/,
                dataIndex: "keyboard",
                width: 100,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000114",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 页面区域编码*/,
                dataIndex: "pageareacode",
                width: 100,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000101",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 按钮功能描述*/,
                dataIndex: "btndesc",
                width: 100,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000102",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 操作*/,
                dataIndex: "operation",
                render: (text, record) => {
                    const { isenable } = record;
                    return (
                        <div className="editable-row-operations">
                            <a
                                className="margin-right-5"
                                onClick={() => this.btnActive(record)}
                            >
                                {isenable
                                    ? langCheck(
                                          "101818AM-000072",
                                          "pages",
                                          props.langJson
                                      )
                                    : langCheck(
                                          "101818AM-000047",
                                          "pages",
                                          props.langJson
                                      )}
                                {/* 国际化处理： 停用,启用*/}
                            </a>
                        </div>
                    );
                }
            }
        ];
        this.columnsSt = [
            {
                title: langCheck(
                    "101818AM-000057",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 序号*/,
                dataIndex: "num",
                width: 50
            },
            {
                title: langCheck(
                    "101818AM-000103",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 模板编码*/,
                dataIndex: "code",
                width: 200,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000104",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 模板名称*/,
                dataIndex: "name",
                width: 200,
                render: (text, record) => this.renderColumns(text, record)
            },
            {
                title: langCheck(
                    "101818AM-000053",
                    "pages",
                    props.langJson
                ) /* 国际化处理： 多语字段*/,
                dataIndex: "resid",
                width: 200,
                render: (text, record) => this.renderColumns(text, record)
            }
        ];
        this.cacheData;
    }
    // 按钮起停用
    btnActive = record => {
        record.isenable = !record.isenable;
        let { langJson } = this.props;
        Ajax({
            url: `/nccloud/platform/appregister/editbutton.do`,
            info: {
                name: langCheck(
                    "101818AM-000061",
                    "pages",
                    langJson
                ) /* 国际化处理： 应用管理*/,
                action: langCheck(
                    "101818AM-000105",
                    "pages",
                    langJson
                ) /* 国际化处理： 按钮启停用*/
            },
            data: record,
            success: ({ data: { data } }) => {
                if (data) {
                    let { appButtonVOs, setPageButtonData } = this.props;
                    appButtonVOs = appButtonVOs.map(item => {
                        if (item.pk_btn === data.pk_btn) {
                            item = data;
                        }
                        return item;
                    });
                    setPageButtonData(appButtonVOs);
                    Notice({
                        status: "simpleSuccess",
                        msg: data.isenable
                            ? langCheck(
                                  "101818AM-000109",
                                  "pages",
                                  langJson
                              ) /* 国际化处理： 启用成功 */
                            : langCheck(
                                  "101818AM-000110",
                                  "pages",
                                  langJson
                              ) /* 国际化处理： 停用成功 */
                    });
                    // 如有模板缓存 则清空
                    let { nodeData } = this.props;
                    if (nodeData && nodeData.parentcode && data.pagecode) {
                        if (
                            getStore(
                                `appTempletStorage_${nodeData.parentcode}_${
                                    data.pagecode
                                }`
                            )
                        ) {
                            const item = `appTempletStorage_${nodeData.parentcode}_${
                                data.pagecode
                            }`;
                            window.localStorage.removeItem(item);
                        }
                    }
                }
            }
        });
    };
    /**
     * 单元格渲染
     * @param {String} text  单元格value
     * @param {Object} record 单元格对象
     * @param {type} type 单元格类型
     * @param {String} selectType 下拉类型 是按钮类型还是按钮颜色
     */
    renderColumns(text, record, type = "input", selectType = "type") {
        record = _.cloneDeep(record);
        if (type === "input") {
            return <div>{text}</div>;
        } else if (type === "select") {
            let { BTNTYPEOPTIONS, BTNCOLOROPTIONS } = this.dataFun();
            let cellText =
                selectType === "type"
                    ? BTNTYPEOPTIONS.find(item => item.value == text)
                    : BTNCOLOROPTIONS.find(item => item.value == text);
            cellText = cellText ? cellText.text : "";
            return <div>{cellText}</div>;
        }
    }
    // 下拉类型的数据
    dataFun = () => {
        let { langJson } = this.props;
        const BTNTYPEOPTIONS = [
            {
                value: "general_btn",
                text: langCheck(
                    "101818AM-000113",
                    "pages",
                    langJson
                ) /* 国际化处理： 常规按钮*/
            },
            {
                value: "buttongroup",
                text: langCheck(
                    "101818AM-000092",
                    "pages",
                    langJson
                ) /* 国际化处理： 按钮组*/
            },
            {
                value: "dropdown",
                text: langCheck(
                    "101818AM-000093",
                    "pages",
                    langJson
                ) /* 国际化处理： 下拉按钮*/
            },
            {
                value: "divider",
                text: langCheck(
                    "101818AM-000094",
                    "pages",
                    langJson
                ) /* 国际化处理： 分割下拉按钮*/
            },
            {
                value: "more",
                text: langCheck(
                    "101818AM-000095",
                    "pages",
                    langJson
                ) /* 国际化处理： 更多按钮*/
            }
        ];
        const BTNCOLOROPTIONS = [
            {
                value: "button_main",
                text: langCheck(
                    "101818AM-000090",
                    "pages",
                    langJson
                ) /* 国际化处理： 主要按钮*/
            },
            {
                value: "button_secondary",
                text: langCheck(
                    "101818AM-000091",
                    "pages",
                    langJson
                ) /* 国际化处理： 次要按钮*/
            }
        ];
        return { BTNTYPEOPTIONS, BTNCOLOROPTIONS };
    };
    render() {
        let {
            appButtonVOs = [],
            pageTemplets = [],
            langJson = {}
        } = this.props;
        return (
            <div>
                <Tabs
                    onChange={activeKey => {
                        this.props.setPageActiveKey(activeKey);
                    }}
                    activeKey={this.props.pageActiveKey}
                >
                    <TabPane
                        tab={langCheck("101818AM-000106", "pages", langJson)}
                        key="1"
                    >
                        {/* 国际化处理： 按钮注册*/}
                        <TableResizableColumn
                            bordered
                            locale={{
                                emptyText: (
                                    <CoverPosotion type="treeCardTable" />
                                )
                            }}
                            pagination={false}
                            rowKey="btnorder"
                            dataSource={appButtonVOs.map((item, index) => {
                                item.num = item.btnorder;
                                return item;
                            })}
                            columns={this.columnsBtn}
                            size="middle"
                        />
                    </TabPane>
                    <TabPane
                        tab={langCheck("101818AM-000107", "pages", langJson)}
                        key="2"
                    >
                        {/* 国际化处理： 页面模板注册*/}
                        <TableResizableColumn
                            bordered
                            locale={{
                                emptyText: (
                                    <CoverPosotion type="treeCardTable" />
                                )
                            }}
                            pagination={false}
                            rowKey="num"
                            dataSource={pageTemplets.map((item, index) => {
                                item.num = index + 1;
                                return item;
                            })}
                            columns={this.columnsSt}
                            size="middle"
                        />
                    </TabPane>
                </Tabs>
            </div>
        );
    }
}
PageTable.propTypes = {
    isNew: PropTypes.bool.isRequired,
    nodeInfo: PropTypes.object.isRequired,
    nodeData: PropTypes.object.isRequired,
    appButtonVOs: PropTypes.array.isRequired,
    pageTemplets: PropTypes.array.isRequired,
    setPageTemplateData: PropTypes.func.isRequired,
    setPageButtonData: PropTypes.func.isRequired,
    setPageActiveKey: PropTypes.func.isRequired
};
export default connect(
    state => {
        return {
            isNew: state.AppManagementData.isNew,
            nodeData: state.AppManagementData.nodeData,
            nodeInfo: state.AppManagementData.nodeInfo,
            pageTemplets: state.AppManagementData.pageTemplets,
            appButtonVOs: state.AppManagementData.appButtonVOs,
            pageActiveKey: state.AppManagementData.pageActiveKey,
            langJson: state.AppManagementData.langJson
        };
    },
    { setPageButtonData, setPageTemplateData, setPageActiveKey }
)(withRouter(PageTable));
